import React from 'react';
import '../App.css';
import { Link } from 'react-router-dom';
const Footer = (props) => {

  const footerData = props.product.career;
  return (
      <footer className="">
        <div className='d-lg-flex p-5' style={{backgroundColor:"#e6e4e4"}}>
          <div className='col-lg-3 mr-lg-5 col-md-6 col-xs-12'>
            <img src={footerData.footerImage.url} height="40px" alt='footer'></img>
            <p className='mt-5 text-secondary'>{footerData.footerDescription}</p>
          </div>
          <div className='col-lg-3 col-md-6 col-xs-12'>
            <h5 className='text-dark'>{footerData.footerlinksHead}</h5>
            <ul className='list-inline d-flex' id='keylinks'>
              <div className='mr-5'>
                <li><Link to='/aboutus' className='text-secondary'><i className="bi bi-chevron-double-right"></i>&nbsp;{footerData.link1}</Link></li>
                <li><Link to='/services' className='text-secondary'><i className="bi bi-chevron-double-right"></i>&nbsp;{footerData.link2}</Link></li>
                <li><Link to='/solutions' className='text-secondary'><i className="bi bi-chevron-double-right"></i>&nbsp;{footerData.link3}</Link></li>
              </div>
              <div>
                <li><Link to='/csr' className='text-secondary'><i className="bi bi-chevron-double-right"></i>&nbsp;{footerData.link4}</Link></li>
                <li><Link to='/careers' className='text-secondary'><i className="bi bi-chevron-double-right"></i>&nbsp;{footerData.link5}</Link></li>
                <li><Link to='/contactus' className='text-secondary'><i className="bi bi-chevron-double-right"></i>&nbsp;{footerData.link6}</Link></li>
              </div>
            </ul>
          </div>
          <div className='col-lg-3 col-md-6 col-xs-12'>
            <h5 className='text-dark'>{footerData.getInTouchHead}</h5>
            <div className='d-flex flex-column'>
              <span className='text-secondary'><i className="bi bi-geo-alt-fill"></i>&nbsp;{footerData.getInTouchLocation}</span>
              <span className='text-secondary'><i className="bi bi-envelope-fill"></i>&nbsp;
                <a href='mailto:info@infomericainc.com' className='text-secondary link-underline link-underline-opacity-0'>{footerData.getInTouchMail}</a>
              </span>
            </div>
          </div>
          <div className='col-lg-3 col-md-6 col-xs-12'>
            <h5 className='text-dark'>{footerData.followUsHead}</h5>
            <div className='mt-2 d-flex'>
              <div className='mr-2 border-0 rounded' style={{ fontSize:"30px"}} id='followusfacebook'>
                <a href='https://www.facebook.com/Infomerica1998/' className='text-primary' target='blank'>
                  <i className="bi bi-facebook"></i>
                </a>
              </div>
              <div className='mr-2 ml-2 border-0 rounded' style={{fontSize:"30px"}} id='followusyoutube'>
                <a href='https://www.youtube.com/results?search_query=infomerica' className='text-danger' target='blank'>
                  <i className="bi bi-youtube"></i>
                </a>
              </div>
              <div className='mr-2 ml-2 border-0 rounded' style={{fontSize:"30px"}} id='followuslinkedin'>
                <a href='https://www.linkedin.com/company/infomerica' className='text-info' target='blank'>
                  <i className="bi bi-linkedin"></i>
                </a>
              </div>
            </div>
          </div>
        </div>

        <div className="footer-copyright text-center">Copyright  2023
          <span className='text-danger'> Infomerica </span>All Rights Reserved. 
          <span className='text-danger'> Terms </span> | <span className='text-danger'> Privacy Policy</span>
        </div>
      </footer>
  );
};
export default Footer;