import React from 'react';
import { Link } from 'react-router-dom';
    
const Navbar = () => {
  return (
    <nav className="navbar navbar-light bg-light sticky-top d-md-flex justify-content-around">
      <div className='d-flex justify-content-around'>
        <button className="navbar-toggler mr-md-3 border-0 text-light" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasDarkNavbar" aria-controls="offcanvasDarkNavbar" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <img src='https://infomericainc.com/Content/images/logo.png' height="60px"  alt='Logo'></img>
      </div>
      <div className='col-8 d-flex justify-content-around '  id='unorderedlist'>
          <Link to='/' className='text-decoration-none text-dark'>HOME</Link>
          <Link to='/aboutus' className='text-decoration-none text-dark'>ABOUTUS</Link>
          <ul id='aboutushover'>
            <li><Link>Vision</Link></li>
            <li><Link>Mission</Link></li>
          </ul>
          <Link to='/industries' className='text-decoration-none text-dark'>INDUSTRIES</Link>
          <Link to='/services' className='text-decoration-none text-dark'>SERVICES</Link>
          <Link to='/solutions' className='text-decoration-none text-dark'>SOLUTIONS</Link>
          <Link to='/contactus' className='text-decoration-none text-dark'>CONTACT US</Link>
          <Link to='/csr' className='text-decoration-none text-dark'>CSR</Link>
          <Link to='/careers' className='text-decoration-none text-dark'>CAREERS</Link>
      </div>

      {/* offcanvas */}
      <div className="offcanvas offcanvas-end bg-info-subtle" tabIndex="-1" id="offcanvasDarkNavbar" aria-labelledby="offcanvasDarkNavbarLabel">
      <div className="offcanvas-header">
        <img src='https://infomericainc.com/Content/images/logo.png' height="60px"  alt='Logo'></img>
        <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
      </div>
      <div className="offcanvas-body">
        <ul className="navbar-nav justify-content-end p-5">
          <li className="nav-item">
            <Link className="nav-link text-dark" to="/"><b>HOME</b></Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link text-dark" to="/aboutus"><b>ABOUTUS</b></Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link text-dark" to="/industries"><b>INDUSTRIES</b></Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link text-dark" to="/services"><b>SERVICES</b></Link>
          </li> 
          <li className="nav-item">
            <Link className="nav-link text-dark" to="/solutions"><b>SOLUTIONS</b></Link>
          </li> 
          <li className="nav-item">
            <Link className="nav-link text-dark" to="/contactus"><b>CONTACT US</b></Link>
          </li> 
          <li className="nav-item">
            <Link className="nav-link text-dark" to="/csr"><b>CSR</b></Link>
          </li> 
          <li className="nav-item">
            <Link className="nav-link text-dark" to="/careers"><b>CAREERS</b></Link>
          </li>
        </ul>
      </div>
    </div>

    </nav>
  );
};
export default Navbar;