import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import './App.css';
import { Query } from 'react-apollo';
import DATA_QUERY from './components/Data/index';
import Navbar from './components/Navbar';
import Footer from './components/Footer';
import RetrievedData from './components/Fulldata';
import Home from './components/Home';
import Aboutus from './components/Aboutus';
import Industries from './components/Industries';
import Services from './components/Services';
import Solutions from './components/Solutions';
import Contactus from './components/Contactus';
import CSR from './components/CSR';
import Careers from './components/Career';

function App() {

  return (
    <Query query={DATA_QUERY}>
     {({ loading, error, data }) => {
      
        if (loading) return <div>Fetching Data.....</div>
        if (error)   return <div>Error Fetching Data</div>

        var items;

        for(let i in data.assets){
          if(data.assets[i].id === "clo408qil0itn0amp48g909ye"){
            items = data.assets[i];
          }
        }

        // console.log(items)
        return (
          <BrowserRouter basename='reactgraphcms'>
            <Navbar/>
            <Routes>
              <Route path='/' element={<Home key={items.id} product={items}/>}></Route>
              <Route path='aboutus' element={<Aboutus key={items.id} product={items} />}></Route>
              <Route path='industries' element={<Industries key={items.id} product={items} />}></Route>
              <Route path='services' element={<Services key={items.id} product={items} />}></Route>
              <Route path='solutions' element={<Solutions key={items.id} product={items} />}></Route>
              <Route path='contactus' element={<Contactus key={items.id} product={items} />}></Route>
              <Route path='csr' element={<CSR key={items.id} product={items} />}></Route>
              <Route path='careers' element={<Careers key={items.id} product={items} />}></Route>
            </Routes>
            <Footer key={items.id} product={items}/>
            {/* <RetrievedData /> */}
          </BrowserRouter>
        );
      }}
    </Query>
  );
}
export default App;