import React from 'react';
import Aos from 'aos';
import "aos/dist/aos.css";

export default function Aboutus(props) {

    Aos.init();
    const aboutusData = props.product.aboutus;
  return (
    <>
    <img src={aboutusData.aboutusBanner.url} width="100%" alt='Aboutus logo'></img>
    <div data-aos="zoom-in">
        <h1 className='text-center m-4'>
            <span className='text-primary'>{aboutusData.aboutHead1}</span>
            <span className='text-danger'> {aboutusData.aboutHead2}</span>
        </h1>
        <div className='m-5'>
            <p className='m-3 fs-5 pb-4'>{aboutusData.aboutDescription1}</p>
            <p className='m-3 fs-5 pb-4'>{aboutusData.aboutDescription2}</p>
            <p className='m-3 fs-5 pb-4'>{aboutusData.aboutDescription3}</p>
            <p className='m-3 fs-5 pb-4'>{aboutusData.aboutDescription4}</p>
            <p className='m-3 fs-5 pb-4'>{aboutusData.aboutDescription5}</p>
        </div>

    </div>
    </>
  )
}
