import React from 'react'
import Aos from 'aos';
import "aos/dist/aos.css"

export default function CSR(props) {
    Aos.init();
  const csrData = props.product.csr;
  return (
    <>
        <img src={csrData.csrBanner.url} width="100%" alt='CSR logo'></img>
        <div data-aos="zoom-in">
            <h1 className='text-center m-4'>
                <span className='text-primary'>{csrData.csrHead1}</span>
                <span className='text-danger'> {csrData.csrHead2}</span>
            </h1>
            <div className='p-5'>
                <div className='p-2 m-3'>
                    <h6 className='text-primary'>{csrData.csrSubh1}</h6>
                    <p><b>{csrData.csrSubh1Description}</b></p>
                    <h6 className='text-primary'>{csrData.csrSubh2}</h6>
                    <p><b>{csrData.csrSubh2Description}</b></p>
                </div>

    {/* center head1 */}
                <h5 className='text-danger text-center m-4'>{csrData.csrCenterh1}</h5>
                <div className='d-flex'>
                    <div className='col-lg-4'>
                        <h6 className='text-center text-primary'><b>{csrData.csrCenterh1Sub1}</b></h6>
                        <p>
                            <span className='text-danger'><b>{csrData.csrCenterh1Sub11}</b></span>
                            <span>{csrData.csrCenterh1Sub11Description}</span>
                        </p>
                        <p>
                            <span className='text-danger'><b>{csrData.csrCenterh1Sub12} </b></span>
                            <span>{csrData.csrCenterh1Sub12Description}</span>
                        </p>
                    </div>

                    <div className='col-lg-4'>
                        <h6 className='text-center text-primary'><b>{csrData.csrCenterh1Sub2}</b></h6>
                        <p>
                            <span className='text-danger'><b>{csrData.csrCenterh1Sub21} </b></span>
                            <span>{csrData.csrCenterh1Sub21Description}</span>
                        </p>
                        <p>
                            <span className='text-danger'><b>{csrData.csrCenterh1Sub22}</b></span>
                            <span>{csrData.csrCenterh1Sub22Description}</span>
                        </p>
                        <p>
                            <span className='text-danger'><b>{csrData.csrCenterh1Sub23}</b></span>
                            <span>{csrData.csrCenterh1Sub23Description}</span>
                        </p>
                    </div>

                    <div className='col-lg-4'>
                        <h6 className='text-center text-primary'><b>{csrData.csrCenterh1Sub3}</b></h6>
                        <p>
                            <span className='text-danger'><b>{csrData.csrCenterh1Sub31}</b></span>
                            <span>{csrData.csrCenterh1Sub31Description}</span>
                        </p>
                        <p>
                            <span className='text-danger'><b>{csrData.csrCenterh1Sub32} </b></span>
                            <span>{csrData.csrCenterh1Sub32Description}</span>
                        </p>
                        <p>
                            <span className='text-danger'><b>{csrData.csrCenterh1Sub33} </b></span>
                            <span>{csrData.csrCenterh1Sub33Description}</span>
                        </p>
                    </div>

                </div>
    {/* center head2 */}
                <h5 className='text-danger text-center m-4'>{csrData.csrCenterh2}</h5>
                <div className='text-center p-5'>
                    <div className='row m-2 pl-5'>
                        <h6 className='text-primary col-lg-3 text-start'>{csrData.csrCenterh2Sub1}</h6>
                        <p className=' col-lg-9 text-start'>{csrData.csrCenterh2Sub1Description}</p> 
                    </div>
                    <div className='row m-2 pl-5'>
                        <h6 className='text-primary col-lg-3 text-start'>{csrData.csrCenterh2Sub2}</h6>
                        <p className=' col-lg-9 text-start'>{csrData.csrCenterh2Sub2Description}</p> 
                    </div>
                    <div className='row m-2 pl-5'>
                        <h6 className='text-primary col-lg-3 text-start'>{csrData.csrCenterh2Sub3}</h6>
                        <p className=' col-lg-9 text-start'>{csrData.csrCenterh2Sub3Description}</p> 
                    </div>
                    <div className='row m-2 pl-5'>
                        <h6 className='text-primary col-lg-3 text-start'>{csrData.csrCenterh2Sub4}</h6>
                        <p className=' col-lg-9 text-start'>{csrData.csrCenterh2Sub4Description}</p> 
                    </div>
                    
                </div>

    {/* center head3 */}
                <h5 className='text-danger text-center m-4'>{csrData.csrCenterh3}</h5>
                <div className='m-4'>
                    <p className='mb-5'><em>{csrData.csrCenterh3Description1}</em></p>
                    <p><strong>{csrData.csrCenterh3Description2}</strong></p>
                </div>
            </div>
        </div>
    </>
  )
}
