import Aos from 'aos';
import "aos/dist/aos.css"
import React from 'react';


const Careers = (props) => {

  Aos.init();
  // console.log(props.product)
  const careerData = props.product.career;


  const handleOption = (e) => {
    const joblocation = e.currentTarget.id;
    if(joblocation === "usa"){
      document.getElementById("usaopeningdisplay").style.display = "block";
      document.getElementById("bangopeningdisplay").style.display = "none";
      document.getElementById("hydopeningdisplay").style.display = "none";
    }
    else if(joblocation === "bang"){
      document.getElementById("bangopeningdisplay").style.display = "block";
      document.getElementById("hydopeningdisplay").style.display = "none";
      document.getElementById("usaopeningdisplay").style.display = "none";
    }
    else if(joblocation === "hyd"){
      document.getElementById("hydopeningdisplay").style.display = "block";
      document.getElementById("bangopeningdisplay").style.display = "none";
      document.getElementById("usaopeningdisplay").style.display = "none";
    }
  }

  const showModal = (e) => {
    const clicked = e.currentTarget.id;
    if(clicked === "ds"){
      document.getElementById("showmod").innerHTML = `
        <div class='shadow rounded p-1 m-1'  style="border-bottom:8px solid red">
          <div class='mb-2 d-flex flex-wrap'>
            <span class='border p-1 m-1'>${careerData.ds1}</span>
            <span class='border p-1 m-1'>${careerData.ds2}</span>
            <span class='border p-1 m-1'>${careerData.ds3}</span>
            <span class='border p-1 m-1'>${careerData.ds4}</span>
            <span class='border p-1 m-1'>${careerData.ds5}</span>
            <span class='border p-1 m-1'>${careerData.ds6}</span>
          </div>
          <h6 class='text-danger text-center'>${careerData.dsHead}</h6>
          <p class='m-4'>${careerData.dsDescription2}</p>
        </div>
      `
    }
    else if(clicked === "reactjs"){
      document.getElementById("showmod").innerHTML = `
        <div class='shadow rounded p-1 m-1' style="border-bottom:8px solid red">
          <div class='mb-2 d-flex flex-wrap'>
            <span class='border p-1 m-1'>${careerData.react1}</span>
            <span class='border p-1 m-1'>${careerData.react2}</span>
            <span class='border p-1 m-1'>${careerData.react3}</span>
            <span class='border p-1 m-1'>${careerData.react4}</span>
            <span class='border p-1 m-1'>${careerData.react5}</span>
            <span class='border p-1 m-1'>${careerData.react6}</span>
            <span class='border p-1 m-1'>${careerData.react7}</span>
          </div>
          <h6 class='text-danger text-center'>${careerData.reactHead}</h6>
          <p class='m-4'>${careerData.reactDescription2}</p>      
        </div>
      `
    }
    else if(clicked === "analyst"){
      document.getElementById("showmod").innerHTML = `
        <div class='shadow rounded p-1 m-1' style="border-bottom:8px solid red">
          <div class='mb-2 d-flex flex-wrap'>
            <span class='border p-1 m-1'>${careerData.analyst1}</span>
            <span class='border p-1 m-1'>${careerData.analyst2}</span>
            <span class='border p-1 m-1'>${careerData.analyst3}</span>
            <span class='border p-1 m-1'>${careerData.analyst4}</span>
            <span class='border p-1 m-1'>${careerData.analyst5}</span>
          </div>
          <h6 class='text-danger text-center'>${careerData.analystHead}</h6>
          <p class='m-4'>${careerData.analystDescription2}</p>      
        </div>
      `
    }
    else if(clicked === "aws"){
      document.getElementById("showmod").innerHTML = `
        <div class='shadow rounded p-1 m-1' style="border-bottom:8px solid red">
          <div class='mb-2 d-flex flex-wrap'>
            <span class='border p-1 m-1'>${careerData.aws1}</span>
            <span class='border p-1 m-1'>${careerData.aws2}</span>
            <span class='border p-1 m-1'>${careerData.aws3}</span>
            <span class='border p-1 m-1'>${careerData.aws4}</span>
            <span class='border p-1 m-1'>${careerData.aws5}</span>
            <span class='border p-1 m-1'>${careerData.aws6}</span>
            <span class='border p-1 m-1'>${careerData.aws7}</span>
          </div>
          <h6 class='text-danger text-center'>${careerData.awsHead}</h6>
          <p class='m-4'>${careerData.awsDescription2}</p>      
        </div>
      `
    }
    else if(clicked === "app"){
      document.getElementById("showmod").innerHTML = `
        <div class='shadow rounded p-1 m-1' style="border-bottom:8px solid red">
          <div class='mb-2 d-flex flex-wrap'>
            <span class='border p-1 m-1'>${careerData.app1}</span>
            <span class='border p-1 m-1'>${careerData.app2}</span>
            <span class='border p-1 m-1'>${careerData.app3}</span>
            <span class='border p-1 m-1'>${careerData.app4}</span>
            <span class='border p-1 m-1'>${careerData.app5}</span>
            <span class='border p-1 m-1'>${careerData.app6}</span>
          </div>
          <h6 class='text-danger text-center'>${careerData.appHead}</h6>
          <p class='m-4'>${careerData.appDescription2}</p>      
        </div>
      `
    }
    else if(clicked === "dga"){
      document.getElementById("showmod").innerHTML = `
        <div class='shadow rounded p-1 m-1' style="border-bottom:8px solid red">
          <div class='mb-2 d-flex flex-wrap'>
            <span class='border p-1 m-1'>${careerData.dga1}</span>
            <span class='border p-1 m-1'>${careerData.dga2}</span>
            <span class='border p-1 m-1'>${careerData.dga3}</span>
            <span class='border p-1 m-1'>${careerData.dga4}</span>
          </div>
          <h6 class='text-danger text-center'>${careerData.dgaHead}</h6>
          <p class='m-4'>${careerData.dgaDescription2}</p>      
        </div>
      `
    }
  };

  return (
      <div className="">
        <img src={props.product.url} width="100%" alt='carrers logo'></img>
        {/* body */}
        <div className='m-5'>
          {/* Careers */}
          <h1 className='text-primary mb-4'>{careerData.careers}</h1>
          <div className='row d-flex flex-column'>
            <div className='d-md-flex'>
              <div className=" shadow rounded m-2 p-4 col-md-6" data-aos="slide-right" data-aos-duration="1500" data-aos-once="true" style={{borderBottom:"8px solid red"}}>
                <img src={careerData.imageConnection.url} height="60px" alt='Connection'></img>
                <p>{careerData.connectionImageDescription}</p>
              </div>
              <div className=" shadow rounded m-2 p-4 col-md-6" data-aos="slide-left" data-aos-duration="1500" data-aos-once="true"  style={{borderBottom:"8px solid red"}}>
                <img src={careerData.imageCloudNetwork.url} height="60px" alt='CloudNetwork'></img>
                <p>{careerData.cloudNetworkDescription}</p>
              </div>
            </div>
            <div className='d-md-flex'>
              <div className=" shadow rounded m-2 p-4 col-md-6" data-aos="slide-right" data-aos-duration="1500" data-aos-once="true"  style={{borderBottom:"8px solid red"}}>
                <img src={careerData.imageComputer.url} height="60px" alt='Computer'></img>
                <p>{careerData.computerDescription}</p>
              </div>
              <div className=" shadow rounded m-2 p-4 col-md-6" data-aos="slide-left" data-aos-duration="1500" data-aos-once="true"  style={{borderBottom:"8px solid red"}}>
                <img src={careerData.imageRocket.url} height="60px" alt='Rocket'></img>
                <p>{careerData.rocketDescription}</p>
              </div>
            </div>
          </div>

          {/* current openings */}
          <h1 className='mb-4 mt-4'>
            <span className='text-primary'>{careerData.currentOpeningsHeading1} </span>
            <span className='text-danger'>{careerData.currentOpeningsHeading2}</span>
          </h1>
          <div className='d-md-flex justify-content-around mt-5'>
            <div>
              <input type='radio' id='usa' onChange={handleOption} name='currentopenings' className='m-1'></input>
              <label className='openings-label border-0 text-info p-2 rounded'>{careerData.usaOpenings}</label>
            </div>
            <div>
              <input type='radio' id='bang' onChange={handleOption} name='currentopenings' className='m-1'></input>
              <label className='openings-label border-0 text-info p-2 rounded'>{careerData.bangOpenings}</label>
            </div>
            <div>
              <input type='radio' id='hyd' onChange={handleOption} name='currentopenings' className='m-1'></input>
              <label className='openings-label border-0 text-info p-2 rounded'>{careerData.hydOpenings}</label>
            </div>
          </div>

          {/* usa opening */}
          <div className='overflow-x-auto' id='usaopeningdisplay'>
            <table className='table table-hover border mt-3'>
              <thead>
              <tr>
                <th>#</th>
                <th>Location</th>
                <th>Job Name</th>
                <th>Skills</th>
                <th>Experience(years)</th>
                <th>No of Positions</th>
                <th></th>
              </tr>
              </thead>
              <tbody>
              <tr>
                <td>{1}</td>
                <td>New York, USA</td>
                <td>{careerData.dsHead}</td>
                <td>{careerData.ds1},{careerData.ds2},{careerData.ds3},{careerData.ds4}</td>
                <td className='text-center'>{5}</td>
                <td className='text-center'>{3}</td>
                <td>
                  <button className='btn-primary border-0 rounded p-2' id='ds' onClick={showModal} data-bs-toggle="modal" data-bs-target="#myModal">{careerData.cobutton}</button>
                </td>
              </tr>
              <tr>
                <td>{2}</td>
                <td>Georgia, USA</td>
                <td>{careerData.reactHead}</td>
                <td>{careerData.react1},{careerData.react2},{careerData.react3},{careerData.react4}</td>
                <td className='text-center'>{3}</td>
                <td className='text-center'>{10}</td>
                <td>
                  <button className='btn-primary border-0 rounded p-2' id='reactjs' onClick={showModal}  data-bs-toggle="modal" data-bs-target="#myModal">{careerData.cobutton}</button>
                </td>
              </tr>
              </tbody>
            </table>
          </div>

          {/* Banglore opening */}
          <div className='overflow-x-auto' id='bangopeningdisplay'>
            <table className='table table-hover border mt-3'>
              <thead>
              <tr>
                <th>#</th>
                <th>Location</th>
                <th>Job Name</th>
                <th>Skills</th>
                <th>Experience(years)</th>
                <th>No of Positions</th>
                <th></th>
              </tr>
              </thead>
              <tbody>
              <tr>
                <td>{1}</td>
                <td>Marathahalli, Banglore</td>
                <td>{careerData.analystHead}</td>
                <td>{careerData.analyst2}, {careerData.analyst3}, {careerData.analyst4}</td>
                <td className='text-center'>{5}</td>
                <td className='text-center'>{2}</td>
                <td>
                  <button className='btn-primary border-0 rounded p-2' id='analyst' onClick={showModal}  data-bs-toggle="modal" data-bs-target="#myModal">{careerData.cobutton}</button>
                </td>
              </tr>
              <tr>
                <td>{2}</td>
                <td>Basavangudi, Banglore</td>
                <td>{careerData.awsHead}</td>
                <td>{careerData.aws1}, {careerData.aws2}, {careerData.aws3}</td>
                <td className='text-center'>{9}</td>
                <td className='text-center'>{12}</td>
                <td>
                  <button className='btn-primary border-0 rounded p-2' id='aws' onClick={showModal}  data-bs-toggle="modal" data-bs-target="#myModal">{careerData.cobutton}</button>
                </td>
              </tr>
              </tbody>
            </table>
          </div>

          {/* Hyderabad opening */}
          <div className='overflow-x-auto' id='hydopeningdisplay'>
            <table className='table table-hover border mt-3'>
              <thead>
              <tr>
                <th>#</th>
                <th>Location</th>
                <th>Job Name</th>
                <th>Skills</th>
                <th>Experience(years)</th>
                <th>No of Positions</th>
                <th></th>
              </tr>
              </thead>
              <tbody>
              <tr>
                <td>{1}</td>
                <td>Panjagutta, Hyderabad</td>
                <td>{careerData.appHead}</td>
                <td>{careerData.app1}, {careerData.app5}, {careerData.app2}</td>
                <td className='text-center'>{10}</td>
                <td className='text-center'>{5}</td>
                <td>
                  <button className='btn-primary border-0 rounded p-2' id='app' onClick={showModal}  data-bs-toggle="modal" data-bs-target="#myModal">{careerData.cobutton}</button>
                </td>
              </tr>
              <tr>
                <td>{2}</td>
                <td>Hitech-city, Hyderabad</td>
                <td>{careerData.dgaHead}</td>
                <td>{careerData.dga2}, {careerData.dga3}</td>
                <td className='text-center'>{6}</td>
                <td className='text-center'>{9}</td>
                <td>
                  <button className='btn-primary border-0 rounded p-2' id='dga' onClick={showModal}  data-bs-toggle="modal" data-bs-target="#myModal">{careerData.cobutton}</button>
                </td>
              </tr>
              </tbody>
            </table>
          </div>
        </div>

        {/* Footer */}
        
        {/* modal */}
        <div className="modal" id="myModal">
          <div className="modal-dialog modal-lg">
            <div className="modal-content">

              <div className="modal-header">
                <button type="button" className="btn-close" data-bs-dismiss="modal"></button>
              </div>

              <div className="modal-body" id='showmod'></div>
              <div className='text-center m-2'>
                <b>{careerData.openingsDetail}</b>
                <span className='border badge fs-4 text-wrap rounded-pill text-dark bg-warning'><b>{careerData.openingsMail}</b></span>
              </div>

              <div className="modal-footer">
                <button className='btn-primary border-0 rounded p-2'>
                  <a href="mailto:info@infomericainc.com" className='text-white link-underline link-underline-opacity-0'>Send Resume</a>
                </button>
                <button type="button" className="btn btn-danger" data-bs-dismiss="modal">Close</button>
              </div>

            </div>
          </div>
      </div>
    </div>
  );
}
export default Careers;